/*------------------------------------*\
    Icon
\*------------------------------------*/

/* a */.icon__link {
  text-decoration: none;
  color: #9DF3C4;
}

/* a */.icon__link i:hover::before {
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
}

/* a */.icon__link i:before {
  display: inline-block;
  -webkit-transition: 1s all;
  -o-transition: 1s all;
  transition: 1s all;
}

@media screen and (max-width: 850px) {
  .icon__link {
    margin: 0px 15px;
  }
}



